:root {
  --bg-body: #dff9fb;
  --bg-nav: #220455;
  --color-nav: #644c89;
  --color-nav-active: #fff;
}

#filter-svg {
  display: none;
}

.navMobile {
  position: relative;
  list-style-type: none;
  display: flex;
  background-color: var(--bg-nav);
  height: 70px;
  /* border-radius: 20px ; */
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  filter: url("#goo");
  /* border-bottom-right-radius: 20px; */
}

.navMobile li a {
  text-decoration: none;
  color: var(--color-nav);
  width: 100px;
  height: 100%;
  display: inline-grid;
  place-items: center;
  font-size: 20px;
  z-index: 1;
  position: relative;
  overflow: hidden;
  margin: auto auto;
  display: grid;
  pointer-events: stroke;
}
.navMobile li {
  width: 25%;
}


.navMobile li a .title {
  display: none;
}

.navMobile li a:hover {
  /* color: var(--bg-body); */
}

.navMobile li a.nav-item-active {
  color: var(--color-nav-active);
  transform: translateY(0%);
  font-size: 25px;
  transition: transform 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.navMobile li a.nav-item-active .title {
  display: block;
  font-size: 15px;
}

