@keyframes zoom {
  0% {
      width: 0px;
      height: 0px;
      opacity: 0.2;
  }
  100% {
      width: 1500px;
      height: 1500px;
      opacity: 0;
  }
}


.main-bg-color {
  background: rgb(125,0,148);
background: linear-gradient(22deg, rgba(125,0,148,1) 0%, rgba(255,138,0,1) 100%);
}

.text-bg-color {
  background: -webkit-linear-gradient(rgba(125,0,148,1),rgba(255,138,0,1));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main-bg-color-flip {
  background: rgb(125,0,148);
  background: linear-gradient(22deg, rgba(255,138,0,1) 0%, rgba(125,0,148,1) 100%);
}
